import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import MsgBoxTools from '../mixins/msgBoxTools';
import FormBanners from '../components/FormBanners.vue';
import MessageBox from '../components/MessageBox.vue';
import PopupConflict from '../components/PopupConflict.vue';
import sleep from '@/mixins/sleep';
import { bannersMapper } from '../module/store';
import ButtonApp from '@/components/ButtonApp.vue';
import vClickOuside from 'v-click-outside';
import animateIfVisible from '../../../mixins/animateIfVisible';
const Mappers = Vue.extend({
    computed: {
        ...bannersMapper.mapState(['form', 'activeAmount', 'bannerCurrent']),
        ...bannersMapper.mapGetters(['listActive', 'formSort', 'bannerById', 'formIsValid', 'isLoading', 'formActiveFrom'])
    },
    methods: {
        ...bannersMapper.mapMutations(['setFormType', 'clearForm', 'setBannerCurrentSuccess', 'setValidationIsShown']),
        ...bannersMapper.mapActions(['getList', 'deleteBanner', 'createBanner', 'deactivateBanner', 'updateFormByBannerData'])
    }
});
let PageCreate = class PageCreate extends Mixins(MsgBoxTools, Mappers) {
    constructor() {
        super(...arguments);
        this.bannerId = null;
        this.secondBtn = null;
        // bannerConflictId: number = null
        this.popupFormIsShown = false;
    }
    get bannerConflict() { return this.listActive.find(b => b.position === this.formSort.value); }
    get bannerConflictId() { return this.bannerConflict && this.bannerConflict.id; }
    get isPageCreate() { return this.$route.path.includes('/banners/create'); }
    get isSmthToCommit() {
        try {
            // check if any field was changed
            const form = this.form.data;
            const isActiveToCommit = !form.find(f => f.name === 'isActive').value;
            const fileToCommit = !!form.find(f => f.name === 'file').value;
            const pageTypeToCommit = form.find(f => f.name === 'pageType').value > 0;
            const sortToCommit = form.find(f => f.name === 'sort').value.toString() !== this.activeAmount.toString();
            const smthElseToCommit = form.filter(f => f.name !== 'isActive' && f.name !== 'sort' && f.name !== 'pageType' && f.name !== 'file').map(f => f.value).some(f => !!f);
            return smthElseToCommit || fileToCommit;
        }
        catch { }
    }
    // @ts-ignore
    get activeFromSplit() { return this.formActiveFrom.value && this.formActiveFrom.value.toString().split('-'); }
    get activeFrom() {
        if (!this.activeFromSplit)
            return;
        const date = new Date();
        date.setDate(Number(this.activeFromSplit[0]));
        date.setMonth(Number(this.activeFromSplit[1]) - 1);
        date.setFullYear(Number(this.activeFromSplit[2]));
        date.setHours(0, 0, 0, 0);
        return date;
    }
    get activeFromTime() { return this.activeFrom && new Date(this.activeFrom).getTime(); }
    get today() {
        const date = new Date();
        date.setHours(0, 0, 0, 0);
        return date;
    }
    get todayTime() { return this.today.getTime(); }
    get isActivatedToday() { return this.activeFromTime === this.todayTime; }
    created() {
        this.setFormType('create');
        document.addEventListener('keydown', this.keydownHandler);
        if (this.bannerCurrent.data)
            this.updateFormByBannerData(this.bannerCurrent.data);
    }
    async mounted() {
        await this.$nextTick();
        animateIfVisible();
    }
    beforeDestroy() {
        document.removeEventListener('keydown', this.keydownHandler);
        this.clearForm();
        this.setBannerCurrentSuccess(null);
    }
    openPopupConflict() {
        document.body.classList.add('modal-open');
        this.popupFormIsShown = true;
    }
    closePopupConflict() {
        document.body.classList.remove('modal-open');
        this.popupFormIsShown = false;
        // this.bannerConflictId = null
    }
    keydownHandler(evt) {
        if (evt.key === 'Escape') {
            if (!this.msgBoxIsShown && !this.popupFormIsShown)
                this.goToPageMain();
            else if (this.popupFormIsShown)
                this.closePopupConflict();
            else if (this.msgBoxIsShown)
                this.closeMsgBox();
        }
    }
    onClickOutside(evt) {
        const targetIsPage = evt.srcElement.classList.contains('page-create');
        if (targetIsPage)
            this.goToPageMain();
    }
    // CLICK HANDLERS
    onSubmit() {
        if (this.formActiveFrom.value && this.bannerConflictId) {
            if (!this.formIsValid)
                this.setValidationIsShown(true);
            else
                this.openPopupConflict();
        }
        else
            this.submitForm();
    }
    onFirstBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'successCreate':
                this.goToPageEdit();
                break;
            case 'failDeactivate':
                this.deactivateBannerConflict();
                break;
            default:
                this.submitForm();
                break;
        }
    }
    onSecondBtnClick() {
        switch (this.requestStatus) {
            case ('successCreate'):
                this.goToPageMain();
                break;
            case 'failDeactivate':
                this.closeMsgBox();
                this.closePopupConflict();
                break;
            default:
                this.closeMsgBox();
                break;
        }
    }
    onConflictConfirm() {
        this.closePopupConflict();
        if (this.formActiveFrom.value)
            this.submitForm();
        else
            this.deactivateBannerConflict();
    }
    goToPageMain() { this.$router.push({ path: '/banners/list' }).catch(err => { }); }
    goToPageEdit() {
        if (!this.bannerCurrent.data)
            return;
        this.$router.push({ path: `/banners/edit/${this.bannerCurrent.data.id}` }).catch(err => { });
    }
    // STORE ACTIONS CALL
    submitForm() {
        this.createBanner()
            .then(async () => {
            this.secondBtn = { type: 'success', isPlain: true };
            this.requestStatus = 'successCreate';
            this.openMsgBox();
            this.$emit('updateList');
            await sleep(1500);
            if (this.isPageCreate) {
                this.closeMsgBox();
                this.goToPageEdit();
            }
        })
            .catch(error => {
            if (this.formIsValid) {
                if (error.bannerId) {
                    this.openPopupConflict();
                }
                else {
                    this.secondBtn = { type: 'danger', isPlain: true };
                    this.requestStatus = 'failCreate';
                    this.openMsgBox();
                }
            }
        });
    }
    deactivateBannerConflict() {
        this.deactivateBanner(this.bannerConflictId)
            .then(() => {
            // this.closeMsgBox()
            this.submitForm();
        })
            .catch(() => {
            this.requestStatus = 'failDeactivate';
            this.secondBtn = { type: 'danger', isPlain: true };
            this.openMsgBox();
        });
    }
};
PageCreate = __decorate([
    Component({
        directives: {
            clickOutside: vClickOuside.directive
        },
        components: {
            FormBanners,
            MessageBox,
            PopupConflict,
            ButtonApp
        }
    })
], PageCreate);
export default PageCreate;
